<!-- 蓝色简洁登录页面 -->
<template>
	<view class="login-bg">
		<image class="img-a" src="@/static/login/bg1.png"></image>
		<view class="t-login">
			<view class="t-b">注 册</view>
			<form class="cl">
				<view class="t-a">
					<image src="@/static/login/user.png"></image>
					<input type="number" name="phone" placeholder="请输入手机号码" maxlength="11" v-model="phone" />
				</view>
				<view class="t-a">
					<image src="@/static/login/pwd.png"></image>
					<input type="password" name="code" maxlength="20" placeholder="请输入密码" v-model="pwd" />
				</view>
				<view class="t-a">
					<image src="@/static/login/yz.png"></image>
					<input type="number" name="code" maxlength="6" placeholder="请输入验证码" v-model="yzm" />
					<view v-if="showText" class="t-c" @tap="getCode()">发送短信</view>
					<view v-else class="t-c" style="background-color: #A7A7A7;">重新发送({{ second }})</view>
				</view>
				
				<view style="display: flex;flex-direction: row;align-items: center;justify-content: space-evenly;padding: 0 20px 20px 20px;">
					<radio @click="change_gender" :checked="gender" color="#ff6c96"></radio>
					<text>男声</text>
					<radio :checked="!gender"color="#ff6c96" @click="change_gender"></radio>
					<text>女声</text>
				</view>
				<wButton
					text="注 册"
					:rotate="isRotate" 
					@click.native="reg()"
					style="background-color: background: linear-gradient(to right, #ff8f77, #fe519f);;"
				></wButton>
			</form>
		</view>
		<view class="cardBox">
			<view>
				已有登录账号？
				<text class="txt" @tap="JumpLogin()">立刻登录</text>
			</view>
		</view>
		<image class="img-b" src="../../static/login/bg2.png"></image>
	</view>
</template>
<script>
	let _this;
	import wButton from '../../components/watch-login/watch-button.vue' ;//button
export default {
	data() {
		return {
			phone: '', //手机号码
			pwd: '' ,//密码
			second: 60, //默认60秒
			showText: true, //判断短信是否发送
			yzm: '' ,//验证码
			isRotate: false, //是否加载旋转
			gender:false
		};
	},
	components:{
		wButton
	},
	mounted() {
		_this= this;
	},
	onLoad() {},
	methods: {
		change_gender(){
			this.gender=!this.gender;
		},
		getCode() {
			var that = this;
			if (!that.phone) {
				uni.showToast({ title: '请输入手机号', icon: 'none' });
				return;
			}
			if (!/^[1][3,4,5,7,8,9][0-9]{9}$/.test(that.phone)) {
				uni.showToast({ title: '请输入正确手机号', icon: 'none' });
				return;
			}
			var interval = setInterval(() => {
				that.showText = false;
				var times = that.second - 1;
				//that.second = times<10?'0'+times:times ;//小于10秒补 0
				that.second = times;
			}, 1000);
			setTimeout(() => {
				clearInterval(interval);
				that.second = 60;
				that.showText = true;
			}, 60000);
			//这里请求后台获取短信验证码
			uni.request({
				url:that.server_url+'/sendsms?user='+that.phone,
				method:'GET',
				success: function(res) {
					that.showText = false;
				}
			});
		},
		reg() {
			_this.isRotate=true
			setTimeout(function(){
				_this.isRotate=false
			},1500)
			var that = this;
			if (!that.phone) {
				uni.showToast({ title: '请输入手机号', icon: 'none' });
				return;
			}
			if (!/^[1][3,4,5,7,8,9][0-9]{9}$/.test(that.phone)) {
				uni.showToast({ title: '请输入正确手机号', icon: 'none' });
				return;
			}
			if (!that.pwd) {
				uni.showToast({ title: '请输入密码', icon: 'none' });
				return;
			}
			if (that.yzm=="") {
				uni.showToast({ title: '请输入验证码', icon: 'none' });
				return;
			}
			if (that.yzm.length!=6) {
				uni.showToast({ title: '验证码格式错误', icon: 'none' });
				return;
			}
			var gender = '';
			if(this.gender==false)	gender='女';
			else 	gender='男';
			console.log(gender);
			
			uni.request({
				url:this.server_url+'/register?user='+this.phone+'&password='+this.pwd+'&code='+this.yzm+'&gender='+gender,
				method:'GET',
				success: (res) => {
					console.log(res);
					if(res.data.result=="注册成功"){
						uni.showToast({
							icon:'success',
							title:'注册成功'
						});
						//保存token,user
						uni.setStorage({
							key:'token',
							data:res.data.token
						});
						uni.setStorage({
							key:'user',
							data:this.phone
						});
						uni.setStorage({
							key:'money',
							data:'0.0'
						});
						uni.setStorageSync('gender',gender);
						//jump
						uni.switchTab({
							url:'../navigation/ShouYe/ShouYe'
						});
					}else{
						uni.showToast({
							icon:'none',
							position:'bottom',
							title:res.data.result
						});
						this.yzm="";
					}
				}
			})
		},
		//立刻注册
		JumpLogin() {
			uni.navigateTo({
				url:'./login'
			})
		}
	}
};
</script>
<style>
.img-a {
	width: 100%;
}
.img-b {
	width: 100%;
	height: 45px;
	bottom: 0;
	position: absolute;
}
.login-bg {
	height: 100vh;
	background: linear-gradient(to bottom, #ff6a9a, #fe7d76);
}

.t-login {
	width: 580rpx;
	padding: 55rpx;
	margin: 0 auto;
	font-size: 28rpx;
	background-color: #ffffff;
	border-radius: 20rpx;
	position: relative;
	margin-top: -200rpx;
	box-shadow: 0 5px 7px 0 rgba(0, 0, 0, 0.15);
	z-index: 9;
}
.t-login button {
	font-size: 28rpx;
	background: linear-gradient(to right, #ff8f77, #fe519f);
	color: #fff;
	height: 90rpx;
	line-height: 90rpx;
	border-radius: 50rpx;
}

.t-login input {
	padding: 0 20rpx 0 120rpx;
	height: 90rpx;
	line-height: 90rpx;
	margin-bottom: 50rpx;
	background: #f6f6f6;
	border: 1px solid #f6f6f6;
	font-size: 28rpx;
	border-radius: 50rpx;
}

.t-login .t-a {
	position: relative;
}

.t-login .t-a image {
	width: 40rpx;
	height: 40rpx;
	position: absolute;
	left: 40rpx;
	top: 28rpx;
	padding-right: 20rpx;
}

.t-login .t-b {
	text-align: left;
	font-size: 46rpx;
	color: #ff939b;
	font-weight: bold;
	margin: 0 0 50rpx 20rpx;
}

.t-login .t-d {
	text-align: center;
	color: #999;
	margin: 80rpx 0;
}

.t-login .t-c {
	position: absolute;
	right: 22rpx;
	top: 22rpx;
	background: #ff6c96;
	color: #fff;
	font-size: 24rpx;
	border-radius: 50rpx;
	height: 50rpx;
	line-height: 50rpx;
	padding: 0 25rpx;
}

.t-login .t-f {
	text-align: center;
	margin: 200rpx 0 0 0;
	color: #666;
}

.t-login .t-f text {
	margin-left: 20rpx;
	color: #aaaaaa;
	font-size: 27rpx;
}

.t-login .uni-input-placeholder {
	color: #aeaeae;
}

.cl {
	zoom: 1;
}

.cl:after {
	clear: both;
	display: block;
	visibility: hidden;
	height: 0;
	content: '\20';
}
.cardBox {
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	-webkit-flex-direction: row;
	flex-direction: row;
	-webkit-box-align: center;
	-webkit-align-items: center;
	align-items: center;
	padding: 5px;
	background: #ffffff;
	opacity: 0.9;
	-webkit-border-radius: 20rpx;
	border-radius: 0 0 20rpx 20rpx;
	height: 70rpx;
	width: 600rpx;
	margin: 0 auto;
	position: relative;
	text-align: center;
	line-height: 70rpx;
	color: #aaa;
	font-size: 28rpx;
}
.cardBox .txt {
	color: #fe519f;
}
</style>
